body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body{ 
  height: 100%; 
  width: 100%; 
  margin: 0; 
  overflow: hidden;
}


#root {
  height: 100%; 
  width: 100%; 
  margin: 0; 
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
  display: flex;
  align-items: center;
  padding: 5px;
  background: #ffffff;
  text-align: left;
  font-weight: bold;
  font-size: 20px;
  color: #282C34;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
}

.photo {
    height: 60px;
    width: 60px;
    padding-right: 50px;
  }

.Geography {
    fill: #EAEAEC;
    outline: none;
}
.searchBar {
  display: flex;
  align-items: center;
  padding: 0px;
  background: #6A6869;
  text-align: left;
  font-size: 20px;
  color: #ffffff;
  top: 70px;
}

.column {
  flex: 33.33% 1;
  height: 30px;
  margin: 0px;
  padding-left: 20px;
  background-color: #cccccc;
  text-align: center;
  padding: 10px;
  vertical-align: middle;
  
}
.modal {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.4);
    transition: 0.3s ease-in-out;
}

.modal.active {
    opacity: 1;
    visibility: visible;
}

.modal__content {
    height: 50%;
    width: 60%;
    margin: 15% auto;
    background-color: #fff;
    border-radius: 5px;
    position: relative;
    transition: inherit;
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
}

.modal.active .modal__content {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.modal__header,
.modal__body,
.modal__footer {
        padding: 16px 20px;
}

.modal__close {
    position: absolute;
    top: 16px;
    right: 20px;
    font-size: 1.5rem;
    cursor: pointer;
    color: #7f7f7f;
}

.modal__close:hover {
    position: absolute;
    top: 16px;
    right: 20px;
    font-size: 1.5rem;
    cursor: pointer;
    color: #000;
}
